import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-home-details',
  templateUrl: 'details.html'
})
export class HomeDetailsPage {

  first = true;

  constructor(public navCtrl: NavController) {
    //console.log(navCtrl);
  }

  back(e) {
    if (this.first) {
      console.log('pop');
      this.navCtrl.pop();
      this.first = false;
    }
  }

}



